import React, {lazy, Suspense} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Loading from "../../../components/shared-components/Loading";
import {Redirect, Route, Switch} from "react-router-dom";
import LayoutContent from "../../../components/layout-components/LayoutContent";

const VillaViews = ({match})=> {

    return (
        <>
            <LayoutContent style={{marginTop: 120, minHeight: "calc(100vh)"}}>
                <Suspense fallback={<Loading cover="content"/>}>
                    <Switch>
                        <Route path={`${ match.url }`} exact component={lazy(() => import(`./villa-all/index`))} />
                        <Route path={`${ match.url }/recomendation`} component={lazy(()=> import("./villa-recomendation/index"))}/>
                        <Route path={`${match.url}/p/:id`} component={lazy(()=> import("./villa-single/index"))}/>
                        <Route path={`${ match.url }/:id/:slug`} component={lazy(()=> import("./villa-single/index"))}/>
                        {/*<Redirect to={`${match.url}/all`} from={match.url}/>*/}
                    </Switch>
                </Suspense>
            </LayoutContent>
        </>
    )
}

VillaViews.propTypes = {}
VillaViews.defaultProps = {}

export default connect(()=> {
    return {}
},{})(VillaViews)
